import {
  Component,
  ChangeDetectorRef,
  Input,
  Output,
  EventEmitter,
  HostListener,
  OnInit,
  ViewChild,
  forwardRef,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";

import { Platform } from 'ionic-angular';
import { TAG_COLORS } from "./colors";


export const CITY_PICKER_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => IonTagsInput),
  multi: true
};

@Component({
  selector: 'ion-tags-input',
  providers: [CITY_PICKER_VALUE_ACCESSOR],
  template: `
  <div class="iti-tags-wrap">
    <ion-tag  *ngFor="let tag of _tags; let $index = index;"
              [tag]="tag"
              [mode]="mode"
              [color]="cssColor"
              [allowClear]="!hideRemove && !readonly"
              (onClear)="btnRemoveTag($index)">
    </ion-tag>
  </div>
  <input #tagsInput
         [hidden]="readonly"
         [disabled]="readonly"
         class="iti-input" [type]="type"
         [placeholder]="placeholder"
         [(ngModel)]="_editTag"
         (blur)="_blur()"
         (keyup.backspace)="keyRemoveTag($event); false"
         (keyup)="separatorStrAddTag()"
         (keyup.enter)="keyAddTag()">
  `,
  host: {
    'class': 'tit-border-color ion-tags-input',
    '[style.border-bottom-color]': '_isFocus ? cssColor : null',
    '[class.active]': '_isFocus',
    '[class.readonly]': 'readonly'
  },
  encapsulation: ViewEncapsulation.None,
  styles: [`.ion-tags-input{border-bottom:1px solid #e5e5e5;-webkit-transition:border-bottom-color .15s ease;transition:border-bottom-color .15s ease;padding:5px;display:block}.ion-tags-input .iti-tag-color{background-color:#4a8bfc;color:#fff}.ion-tags-input.active{border-bottom-color:#4a8bfc}.ion-tags-input .iti-tag{display:block;float:left;font-family:sans-serif;font-size:1.3rem;font-weight:400;margin-right:5px;margin-bottom:5px;padding:4px 10px;max-height:30px;border-radius:0}.ion-tags-input .iti-tag.iti-tag-ios{border-radius:13px}.ion-tags-input .iti-tag.iti-tag-md{border-radius:4px}.ion-tags-input .iti-input{background:0 0;border:0;color:#777;font-family:sans-serif;font-size:13px;font-weight:400;outline:0;padding:5px;width:80px}.ion-tags-input a.iti-tag-rm::before{content:" x";cursor:pointer;font-weight:700;color:#fff}.ion-tags-input.active.tit-border-color.readonly,.ion-tags-input.tit-border-color.readonly{border:none}`]
})
export class IonTagsInput implements ControlValueAccessor, OnInit {

  @Input() mode: string = '';
  @Input() readonly: boolean = false;
  @Input() hideRemove: boolean = false;
  @Input() maxTags: number = -1;
  @Input() placeholder: string = '+Tag';
  @Input() type: string = 'text';
  @Input() separatorStr: string = ',';
  @Input() once: boolean = true;
  @Input() canEnterAdd: boolean = true;
  @Input() canBackspaceRemove: boolean = true;
  @Input() verifyMethod: (tagSrt: string) => boolean;
  @Input()
  set color(value: string) {
    if (TAG_COLORS.hasOwnProperty(value)) {
      this.cssColor = (TAG_COLORS[value] as string);
    } else {
      this.cssColor = value;
    }
  }

  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Output() ionFocus: EventEmitter<any> = new EventEmitter();
  @Output() ionBlur: EventEmitter<any> = new EventEmitter();
  @ViewChild('tagsInput') input: any;

  _editTag: string = '';
  _tags: Array<string> = [];
  _isFocus: boolean = false;
  _onChanged: Function;
  _onTouched: Function;
  cssColor: string;

  constructor(public plt: Platform, public ref: ChangeDetectorRef) {
  }

  ngOnInit(): void {
    if (this.mode === '') {
      this.plt.ready().then(() => {
        this.initMode();
      })
    }
  }

  keyAddTag(): any {
    let tagStr = this._editTag.trim();
    if (!this.canEnterAdd) return;
    if (!this.verifyTag(tagStr)) return;
    if (!this.isOnce(tagStr)) {
      this._editTag = '';
      return;
    }
    this.pushTag(tagStr);
  }

  separatorStrAddTag(): any {
    const lastIndex: number = this._editTag.length - 1;
    let tagStr: string = '';
    if (!this.separatorStr) return;

    if (this._editTag[lastIndex] === this.separatorStr) {
      tagStr = this._editTag.split(this.separatorStr)[0].trim();

      if (this.verifyTag(tagStr) && this.isOnce(tagStr)) {
        this.pushTag(tagStr);
      } else {
        this._editTag = '';
      }
    }
  }

  keyRemoveTag(): any {
    if (!this.canBackspaceRemove) return;
    if (this._editTag === '') {
      this.removeTag(-1);
      this._editTag = '';
    }
  }

  btnRemoveTag($index: number): any {
    this.removeTag($index);
  }

  verifyTag(tagStr: string): boolean {

    if (typeof this.verifyMethod === 'function') {
      if (!this.verifyMethod(tagStr)) {
        this._editTag = '';
        return false;
      } else {
        return true;
      }
    }

    if (!tagStr.trim()) {
      this._editTag = '';
      return false;
    } else {
      return true;
    }
  }

  pushTag(tagStr: string): any {
    if (this.maxTags !== -1 && this._tags.length >= this.maxTags) {
      this._editTag = '';
      return;
    }
    this._tags.push(tagStr.trim());
    this.ref.detectChanges();
    this.onChange.emit(this._tags);
    this._editTag = '';
  }

  removeTag($index: number): any {
    if (this._tags.length > 0) {
      if ($index === -1) {
        this._tags.pop();
        this.onChange.emit(this._tags);
      } else if ($index > -1) {
        this._tags.splice($index, 1);
        this.onChange.emit(this._tags);
      }
    }
  }

  isOnce(tagStr: string): boolean {
    if (!this.once) return true;
    const tags: string[] = this._tags;
    return tags.every((e: string): boolean => {
      return e !== tagStr
    })
  }

  @HostListener('click', ['$event'])
  _click(ev: UIEvent): any {
    if (!this._isFocus) {

    }
    this._focus();
    ev.preventDefault();
    ev.stopPropagation();
  }

  _blur(): any {
    if (this._isFocus) {
      this._isFocus = false;
      this.ionBlur.emit(this._tags);
    }
  }

  _focus(): any {
    if (!this._isFocus) {
      this._isFocus = true;
      this.input.nativeElement.focus();
      this.ionFocus.emit(this._tags);
    }
  }

  writeValue(val: any): void {
    this._tags = val;
  }

  registerOnChange(fn: any): void {
    this._onChanged = fn;
    this.setValue(this._tags);
  }

  registerOnTouched(fn: any): void {
    this._onTouched = fn;
  }

  setValue(val: any): any {
    this._tags = val;
    if (this._tags) {
      this._onChanged(this._tags);
    }
  }

  initMode(): any {
    this.mode = this.plt.is('ios') ? 'ios' : this.plt.is('android') ? 'md' : this.plt.is('windows') ? 'mp' : 'md';
  }

}
